div.app {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	width: 90%;
	margin: 0 auto;
}

header {
	flex-basis: 30vh;
}

main {
	display: flex;
	flex-grow: 1;
	padding: 30px 0px;
}

section.page {
	flex-grow: 1;
}

aside {
	min-width: 240px;
}

footer {
	background-color: black;
	padding: 10px 0px;
	text-align: center;
	color: white;
	text-transform: uppercase;
}
